import favourite from '../img/favourite.svg';
import plus from '../img/plus.svg';
/*import add from '../img/add.svg'; */
import React from 'react';


const Product =()=>{
     return(
     		 <>
     		    <div className="favourite"> 
				  <div className="fav-wrapper">
					  <img src={favourite} alt="good" />
					  <p className="price">₦890</p>
					  <p className="name">Three Crowns</p>
					  <p className="weigth">450g</p>
				  </div>
				  <span className="add-to-cart" id="add-to-cat"><img src={plus} /></span>
    
                </div>
     		 </>
     	)
}

export default Product