import React from "react";
import { Link, Router } from "react-router-dom";
import Product from "./../component/Product";
import place from "../img/place.svg";
import get from "../img/get.svg";
import unbeatable from "../img/unbeatable.svg";
import jeetar from "../img/jeetar.svg";
import iphone from "../img/iphone.svg";
import android from "../img/android.svg";

const ProductList = () => {
	return (
		<>
			<div className="favourite-section container-fluid">
				<div className="favourite-top my-5">
					<div className="row">
						<div className="col-12">
							<div className="text-center tw-flex tw-justify-center tw-items-center">
								<img src={jeetar} alt="logo" className="jeetar" />
								<h2 className="d-lg-inline d-md-inline">
									There’s more love in the app.
								</h2>
								<div className="tw-flex tw-items-center">
									<img src={iphone} alt="logo" />
									<img className="tw-ml-5" src={android} alt="logo" />
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="row">
					<div className="col-lg-12 col-md-12 col-sm-12 pb-5 pt-3">
						<h6 className="favourite-head">Favourites</h6>
					</div>
				</div>
				<div className="favourite-container">
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
				</div>

				<div className="favourite-buttom py-5">
					<div className="row">
						<div className="col-sm-12 col-md-4 col-lg-4">
							<div className="place-order text-center my-5">
								<img src={place} alt="place" />
								<h6>Place an Order, Whenever</h6>
								<p>We’ve got you covered whenever, however 24/7</p>
							</div>
						</div>
						<div className="col-sm-12 col-md-4 col-lg-4">
							<div className="get-order text-center my-5">
								<img src={get} alt="get" />
								<h6>Get Your Order in Less Than 20mins </h6>
								<p>Time matters to us, we operate at the speed of light.</p>
							</div>
						</div>
						<div className="col-sm-12 col-md-4 col-lg-4">
							<div className="unbeatable text-center my-5">
								<img src={unbeatable} alt="unbeatable" />
								<h6>Unbeatable Prices</h6>
								<p>
									Don’t break your bank while placing orders online, jeetar is
									here.
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default ProductList;
