import React from 'react';

const Category=({img, title, slug})=>{
     return(
     		 <>
     		   <div className="category">
					<a href={slug}>
						<div className="cat-wrapper">
						  <img src={img} alt={title} className="category-image" />
						</div>
						<p className="category-caption">{title}</p>
					</a>
				</div>
     		 </>
     	);
}

export default Category